<template>
    <div class="v-container">
        <!-- 面包屑 -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ name: 'runtimeAll' }">运行状态</el-breadcrumb-item>
            <el-breadcrumb-item>运行总览</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <div class="msg-left-frame">
            <header>
                通讯录
            </header>
            <div class="address-box" style=" overflow: auto;">
                <el-collapse v-model="activeNames" @change="handleChange">
                    <el-collapse-item title="群组" name="1">
                        <li class="address-li">
                            <img src="../../../static/img/header/header.jpg">
                            <p>ssss</p>
                        </li>
                        <li class="address-li">
                            <img src="../../../static/img/header/header.jpg">
                            <p>ssss</p>
                        </li>
                        <li class="address-li">
                            <img src="../../../static/img/header/header.jpg">
                            <p>ssss</p>
                        </li>
                        <li class="address-li">
                            <img src="../../../static/img/header/header.jpg">
                            <p>ssss</p>
                        </li>
                        <li class="address-li">
                            <img src="../../../static/img/header/header.jpg">
                            <p>ssss</p>
                        </li>
                    </el-collapse-item>
                    <el-collapse-item title="通讯录" name="2">
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                    </el-collapse-item>
                    <el-collapse-item title="其他" name="3">
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                    </el-collapse-item>
                    <el-collapse-item title="小组" name="4">
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                        <li class="address-li"><p>ssss</p></li>
                    </el-collapse-item>
                </el-collapse>
            </div>
        </div>

        <div class="msg-frame">
            <header>
                <div class="logo">
                    <img src="../../../static/img/header/header.jpg">
                </div>
                {{chatObj.name}}
            </header>
            <div class="msg-box">
                <ul class="msg-ul" ref="chatContent">
                    <li class="muchlist" v-if="isHasHistoryMsg">
                        <a href="javascript:void(0)" @click="historymsg">查看更多历史消息</a>
                    </li>
                    <li class="muchlist" v-else>
                        <a>没有更多消息了</a>
                    </li>
                    <li v-for="msg in msgs" :class="{right:msg.right}" class="all-list">
                        <img src="../../../static/img/header/header.jpg">
                        <p>{{msg.text}}</p>
                    </li>
                </ul>
            </div>
            <div class="send-box">
                <el-input v-model="sendMsg" placeholder="请输入内容">
                    <template slot="append">
                        <el-button type="primary" @click=websocketsend>消息发送</el-button>
                    </template>
                </el-input>
            </div>
        </div>


    </div>
</template>

<script>

    export default {
        name: 'runtimeAll',
        data() {
            return {
                isHasHistoryMsg: true,
                historyMsgs: [{
                    text: "你好1",
                    right: 1
                }],
                msgs: [
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },
                    {
                        text: "数据已",
                        right: 0
                    },

                ],
                senderId: null,
                sendMsg: null,
                chatObj: {
                    id: null,
                    name: null,
                    path: null,
                }
            }
        },
        created() {
            this.getCurChatObj();
            this.initWebSocket();
        },
        destroyed() {
            this.websock.close() //离开路由之后断开websocket连接
        },
        watch: {
            msgs(newval, oldval) {
                let _this = this;
                setTimeout(function () {
                    _this.$refs.chatContent.scrollTop = _this.$refs.chatContent.scrollHeight;
                }, 100)
            }
        },
        methods: {
            getCurChatObj() {
                this.chatObj.id = 'group_chat_1';
                this.chatObj.name = '群聊1';
                this.senderId = this.$store.getters.getLoginInfo.userInfo.id;
            },
            historymsg() {
                this.isHasHistoryMsg = false;
                for (let i = 0; i < this.historyMsgs.length; i++) {
                    this.msgs.unshift(this.historyMsgs.reverse()[i]);
                }
            },
            initWebSocket() { //初始化weosocket
                // const wsuri = "ws://127.0.0.1:8007/OAMP/socketService";
                this.websock = new WebSocket(this.$socketCHATURI);
                this.websock.onmessage = this.websocketonmessage;
                this.websock.onopen = this.websocketonopen;
                this.websock.onerror = this.websocketonerror;
                this.websock.onclose = this.websocketclose;
            },
            websocketonopen() {
                // this.$refs.chatContent.scrollTop = this.$refs.chatContent.scrollHeight;
                this.sendMsg = '用户进入';
                this.websock.send(JSON.stringify(this.$createSocketData(this.$enum.CHAT_MODE.GROUP, this.chatObj.id, this.senderId, this.sendMsg)));
            },
            websocketonerror() {
                //连接建立失败重连
                this.initWebSocket();
            },
            websocketonmessage(e) {
                //数据接收
                let receive_data = JSON.parse(e.data);
                this.msgs.push({right: receive_data.senderId === this.senderId, text: receive_data.msg});
            },
            websocketsend() {
                //数据发送
                if (this.sendMsg !== null && this.chatObj.id !== null) {
                    this.websock.send(JSON.stringify(this.$createSocketData(this.$enum.CHAT_MODE.GROUP, this.chatObj.id, this.senderId, this.sendMsg)));
                    this.$refs.chatContent.scrollTop = this.$refs.chatContent.scrollHeight;
                    this.sendMsg = null;
                }
            },
            websocketclose(e) {
                //关闭
                console.log('断开连接', e);
            },
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    header {
        width: 100%;
        height: 45px;
        color: white;
        font-size: 15px;
        position: relative;
        text-align: center;
        background: #40a2fe;
        line-height: 45px;
    }

    header .logo {
        position: absolute;
        width: 33px;
        height: 33px;
        border-radius: 50%;
        left: 10px;
        top: 6px;
    }

    .logo img {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 50%;
    }

    .msg-left-frame {
        position: absolute;
        background-color: #ffffff;
        left: 25%;
        right: 35%;
        width: 14%;
        height: 81%;
    }

    .msg-frame {
        position: absolute;
        background-color: #606266;
        left: 40%;
        right: 35%;
        width: 30%;
        height: 80%;
    }

    .msg-box {
        position: absolute;
        top: 45px;
        left: 0;
        right: 0;
        bottom: 45px;
    }

    .address-box {
        position: absolute;
        top: 45px;
        left: 15px;
        right: 0;
        bottom: 0;
    }

    .address-li {
        height: 32px;
        list-style-type: none;
        padding-left: 20px;
    }

    .address-li img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: block;
        margin-right: 10px;
        float: left;
    }

    .address-li p {
        font-size: 16px;
        margin-top: 6px;
    }

    .send-box {
        position: absolute;
        height: 30px;
        width: 100%;
        left: 0;
        bottom: 0;
        background: #CECECE;
        text-align: center;
    }

    .msg-ul {
        height: 100%;
        overflow: auto;
        margin-left: -40px;
    }

    ul {
    }

    ul li {
        font-size: 12px;
        list-style: none;
        overflow: auto;
    }

    ul .all-list {
    }

    ul .all-list img {
        width: 45px;
        height: 45px;
        border-radius: 50%;
        display: block;
        margin: 12px;
        float: left;
    }

    ul .all-list p {
        max-width: 340px;
        padding: 12px;
        float: left;
        background: #a2d6f3;
        color: black;
        font-size: 16px;
        font-family: 楷体;
        border: 1px solid #91c9e9;
        border-radius: 5px;
        box-shadow: 2px 2px #369bdb;
        margin-top: 5px;
        word-wrap: break-word;
        word-break: normal;
    }

    ul .muchlist {
        padding: 10px;
        text-align: center;
    }

    ul li a {
        padding: 5px 10px;
        background: #e2e2e2;
        text-decoration: none;
        font-size: 12px;
        border-radius: 5px;
        color: #666;
    }

    ul li a:hover {
        text-decoration: none;
    }

    ul .right p {
        box-shadow: -2px 2px #369bdb;
        float: right;
    }

    ul .right img {
        float: right;
    }

    .row-txt {
        padding: 10px 5px 5px;
        border-radius: 4px;
        margin: 5px 5px;
        background-color: #666666;
    }

    .el-collapse-item__header {
        padding: 10px;
        font-size: 20px;
    }
</style>



